@import url(equal-height.css);

$primary-blue: #26374a;
$primary-blue-dark: #071523;
$primary-chart: #37b99c;

main {
    padding-bottom:50px;
    scroll-behavior: auto!important;
}

#navbar {
    
    background-color: $primary-blue;    
    color: #fff;
    a:link,
    a:visited,
    a:hover,
    a:active {
        color:#fff;
    }
    float: right;
    z-index: 2;
    margin:15px 30px;
 }


.header {
    width:100%;
    background-color: $primary-blue;
    color: #fff;
    padding: 25px 30px 10px;
    margin: 0;

    h1 {
        padding:0;
        margin:0;
        border: none;
        border: none !important;
    }
}

h3::after {
    border-bottom: .18em solid $primary-chart;
    content: "";
    display: block;
    padding-bottom: 8px;
    width: 70px;
    // scroll-margin-top:45px;
}

.panel h3:after {
    border: none;
    scroll-margin-top:unset;
    width:100%;
    padding:0;
}


ul {
    margin-left:15px;
}

details details {
    margin-top:30px;
}
summary {
    margin-bottom:15px;
}
.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}
.content {
    margin:0 30px;
    position:relative;
}

.dashboard-navigation {
    overflow:hidden;
    z-index:99;
    width:100%;
    background-color: $primary-blue;
    display: flex;
    align-items: end;
    padding:5px 30px;
    margin:0;     
    color:#fff;
    height:45px;


    label {
        margin:0;
        font-weight:normal;
    }
    select {
        background-color: transparent;
        margin-left: 15px;  
        border: none;   
        border-bottom: 2px solid #fff;
    }

    option {        
        color:#000;
    }
    &::after {
        clear:left;
    }    
    
}

caption {
    caption-side: top !important;
}

svg {
    width: 100% !important;
}

canvas:not(.no-min-height) {
    max-height: 250px;
}

// .tabpanels details {
//     // padding-top: 15px!important;
// }

.wb-tabs canvas {
    max-height:350px;
}

.wb-tabs [role=tablist]>li {
    background-color: $primary-blue!important;
    display:inline-flex;

    &:not(:last-child) {        
        margin-right:5px;
    }
}

.wb-tabs [role=tablist]>li.active a {
    border-color: $primary-chart;
    background-color: #fff;
    color:#000!important;
}
.wb-tabs [role=tablist]>li a {
    background-color: $primary-blue;
    color:#fff!important;
    flex-grow: 1;
}

@media screen and (min-width: 768px) {
    .table-responsive {
        overflow-x: visible;
    }
}

.large-text {
    font-size: 3em;
    font-weight: bold;
    padding: 15px 25px;
    color: $primary-chart;
    text-transform: uppercase;

    @media screen and (max-width: 1550px) {
        & {
            font-size: 3em;
        }
    }

    @media screen and (max-width: 1100px) {
        & {
            font-size: 2.5em;
        }
    }

    @media screen and (max-width: 450px) {
        & {
            font-size: 2em;
            padding: 10px 25px;
        }
    }
}

.number-change {
    font-size: 1.5em;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-self: flex-end !important;
    width: 100%;
}


.box {
    margin: 25px 0;

    .box-heading h3, .box-heading h2 {
        background-color: $primary-blue;
        border-left: solid 5px $primary-chart;
        padding: 15px 25px;
        margin:0!important;
        color: #fff;
        font-weight: lighter;
    }

    .box-body {
        background-color: #f4f4f4;
        padding: 20px;

        details,
        summary {
            background-color: #fff !important;
        }
    }

    .panel {
        border-radius: 0;
    }
}
.btn-group-lang {
    margin-top:10px;
    button{
        min-width: 100px;
    }
}

.spinner-wrapper {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
    z-index: 2;
    position: absolute;
    top: 0;
}

.spinner {
    z-index:3;
    position: absolute;
    left: 50%;
    top: 5%;
    height:90px;
    width:90px;
    margin:0px auto;
    -webkit-animation: rotation .6s infinite linear;
    -moz-animation: rotation .6s infinite linear;
    -o-animation: rotation .6s infinite linear;
    animation: rotation .6s infinite linear;
    border-left:6px solid rgba(38, 55, 74,.15);
    border-right:6px solid rgba(38, 55, 74,.15);
    border-bottom:6px solid rgba(38, 55, 74,.15);
    border-top:6px solid rgba(38, 55, 74,.8);
    border-radius:100%;
 }
 
 @-webkit-keyframes rotation {
    from {-webkit-transform: rotate(0deg);}
    to {-webkit-transform: rotate(359deg);}
 }
 @-moz-keyframes rotation {
    from {-moz-transform: rotate(0deg);}
    to {-moz-transform: rotate(359deg);}
 }
 @-o-keyframes rotation {
    from {-o-transform: rotate(0deg);}
    to {-o-transform: rotate(359deg);}
 }
 @keyframes rotation {
    from {transform: rotate(0deg);}
    to {transform: rotate(359deg);}
 }